var DISCOVERY_DOCS = [
  "https://www.googleapis.com/discovery/v1/apis/drive/v3/rest",
];
var SCOPES = "https://www.googleapis.com/auth/drive.metadata.readonly";

export const getDoc = async () => {
  return new Promise((resolve) => {
    window.gapi.client.load("sheets", "v4", () => {
      window.gapi.client.sheets.spreadsheets.values
        .get({
          spreadsheetId: process.env.REACT_APP_SPREADSHEET_ID,
          range: "גיליון1!A1",
        })
        .then((response) => {
          alert(response.result);
          if (
            response &&
            response.result &&
            response.result.values &&
            response.result.values.length > 0
          ) {
            resolve(response.result.values);

            // dispatch({ type: "GET_VALUES", payload: response.result.values });
          } else {
            alert("failed");
            console.log("failed");
            resolve(-1);
          }
        })
        .catch((e) => {
          alert(e);
          console.log(e);
          resolve(-1);
        });
    });
  });
};

export const updateDoc = async (valuesPrm, rangePrm) => {
  window.gapi.client.load("sheets", "v4", () => {
    window.gapi.client.sheets.spreadsheets.values
      .update({
        spreadsheetId: process.env.REACT_APP_SPREADSHEET_ID,
        range: "גיליון1!" + rangePrm,
        values: [...valuesPrm],
        valueInputOption: "USER_ENTERED",
      })
      .then((response) => {
        alert("updatedoc response");
        if (
          response &&
          response.result &&
          response.result.values &&
          response.result.values.length > 0
        ) {
          // dispatch({ type: "GET_VALUES", payload: response.result.values });
        } else {
          console.log("failed");
        }
      })
      .catch((e) => {
        console.log(e);
      });
  });
};
export const init = async () => {
  return new Promise((resolve) => {
    window.gapi.load("client", async () => {
      if (window.gapi.client && window.gapi.client.init)
        window.gapi.client
          .init({
            apiKey: "AIzaSyCgX3fnbgdnEV-bo89ZFR4vzVyZfIegehU",
            clientId:
              "575052170802-r35v9eu54hfdqdmisv8aur6sgrksdud1.apps.googleusercontent.com",
            discoveryDocs: DISCOVERY_DOCS,
            scope: SCOPES,
          })
          .then(async () => {
            const res = await getDoc();

            resolve(res);
          })
          .catch((e) => {
            console.log(e);
          });
      else {
        resolve(init());
      }
    });
  });
};
