import React from "react";
import "./Button.css";
import Spinner from "../Spinner/Spinner";
import { updateDoc, init, getDoc } from "../Google/Google";
export default function Feed() {
  const [status, setStatus] = React.useState(-1);
  const [loading, setLoading] = React.useState(true);
  const [error, setError] = React.useState(-1);
  function updateDocHandler() {
    let updateRange = "A1";

    updateDoc([[status > 0 ? -1 : 1]], updateRange);
  }
  async function googleInit() {
    var script = document.createElement("script");
    script.src = "https://apis.google.com/js/api.js";
    script.onload = async () => {
      const res = await init();
      alert(res);
      if (res && res.toString().indexOf("1") > -1) {
        setStatus(parseInt(res[0]));
      }
      setLoading(false);
    };
    document.body.appendChild(script);
  }
  React.useEffect(() => {
    if (!window.gapi) {
      googleInit();
    }
  }, []);
  if (error > 0) return <div className="error_container">{error}</div>;

  return (
    <div className="feed_container">
      {loading && (
        <div className="spinner_container">
          <Spinner />
        </div>
      )}
      {!loading && (
        <div
          className="garvishon"
          style={{
            border: status > 0 ? "1px solid green" : "1px solid red",
          }}
        >
          <div className="garvishon_image">
            <img
              className="image"
              src={
                "data:image/jpeg;base64,"
              }
            />
          </div>
          <br />
          <br />
          <div
            onClick={() => {
              setStatus(status > 0 ? -1 : 1);
              updateDocHandler();
            }}
            className="garvishon_footer"
            style={{
              backgroundColor: status > 0 ? "green" : "red",
              color: "white",
            }}
          >
            {status > 0 ? "ALIVE" : "DEAD"}
          </div>
        </div>
      )}
    </div>
  );
}
